import axios from 'axios'
import router from '@/router'
import VueExample from "../main.js"
// import qs from 'qs'
// 设置带cookie
// axios.defaults.withCredentials = true
// 设置请求头
// axios.defaults.headers.post['Content-Type'] = 
// 'application/json;charset=UTF-8';
// 设置baseURL
let baseURL = require("../../public/projectConfig")
// import { baseURL } from "../../public/projectConfig"
import { getCookie } from '@/utils/auth'
// import { log } from 'util'
// import { Alert } from 'element-ui';
// require("babel-polyfill")
// require('es6-promise').polyfill();
// 创建 axios 实例

const service = axios.create({
	// baseURL: process.env.NODE_ENV === 'production' ? Url : '/api',
	// baseURL: '/api',
	baseURL,
	timeout: 10000, // 请求超时时间

});

// request拦截器
service.interceptors.request.use(
	config => {
		let reqUrl = config.url;
		if (reqUrl.indexOf('login') === -1 && !getCookie("token")) {
			router.push('/login');
			return Promise.reject("您尚未登录")
		}
		config.headers.Authorization = getCookie("token")


		// 权限了拦截
		if (reqUrl.indexOf('login') == -1 && reqUrl.indexOf('getUserInfo') == -1) {
			// let userInfo = this.$store.state.userInfo
			let userInfo = VueExample.$store.state.userInfo
			let blackUrls = userInfo && userInfo.permissionList;
			if (!blackUrls) return config
			for (const item of blackUrls) {
				if (("/" + reqUrl).indexOf(item) > -1) {
					let handle = ""
					let strArr = [{ api: "add", name: "添加" }, { api: "update", name: "修改" }, { api: "del", name: "删除" }, { api: "get", name: "数据" }, { api: "find", name: "数据" }]
					for (const item of strArr) {
						if (reqUrl.indexOf(item.api) > -1) {
							handle = item.name
						}
					}
					setTimeout(() => {
						VueExample.$message.error("您暂无" + (config.name || handle) + "权限")
					});

					return Promise.reject("您暂无" + (config.name || handle) + "权限")
				}
			}
		}
		return config
	},
	error => {
		// eslint-disable-next-line no-console
		console.log(error);
		return Promise.reject(error);
	});

// response拦截器
service.interceptors.response.use(
	response => {
		let res = response.data;
		if (res && response.status == "200") {
			if (res.status && res.status != "200") {
				return Promise.reject(res.msg)
			} else {
				return Promise.resolve(res);
			}


		} else if (res && res.status == '20011') {
			// alert("已退出登陆")
			router.push('/login');
		} else {
			return Promise.reject('res拦截器报错：' + res.msg)
		}
	},
	error => {
		// alert(10009)
		return Promise.reject(error)
	}
);

export default service
