<template>
  <div class="index">
    <UMEN v-show="message"></UMEN>
    <div class="right" :class="{right2:message}">
      <Head :msg="meta" :userName="userName" @showUmen="showUmen"></Head>
      <el-main class="main">
        <el-scrollbar style="height:100%" ref="scroll">
          <keep-alive>
            <router-view v-if="this.$route.meta.keepAlive"></router-view>
          </keep-alive>

          <router-view v-if="!this.$route.meta.keepAlive"></router-view>
        </el-scrollbar>
      </el-main>
    </div>
  </div>
</template>

<script>
import UMEN from "@/components/umen.vue";
import Head from "@/components/headers";
import { decryptDes } from "@/utils/des";

export default {
  name: "index",
  components: {
    UMEN,
    Head
  },
  data() {
    return {
      meta: "",
      message: true,
      userName: "",
      routerFrom: "",
      fromScroll: ""
    };
  },
  mounted() {
    console.log(
      "%c%s",
      "background:#ccc;color:#f00;font-size:20px;",
      "欢迎使用优企创联CRM系统"
    );
    this.meta = this.$route.name;
    // setTimeout(() => {
    let userJson = decryptDes(localStorage.getItem("userInfo"));
    this.$store.commit("userInfo", JSON.parse(userJson));

    console.log(this.$store.state.userInfo);
    this.userName = this.$store.state.userInfo.name;
    // }, 1000);
    // this.userName = this.$store.state.userInfo.name;
  },
  methods: {
    showUmen(data) {
      this.message = data;
    }
  },
  watch: {
    $route(to, from) {
      this.meta = this.$route.name;
      if (from.meta.keepAlive) {
        this.routerFrom = from.path;
        this.fromScroll = this.$refs.scroll.wrap.scrollTop;
      }
      if (to.path === this.routerFrom && !from.meta.keepAlive) {
        this.$refs.scroll.wrap.scrollTop = this.fromScroll;
      } else {
        this.$refs.scroll.wrap.scrollTop = 0;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.index {
  height: 100%;
  display: flex;
}
.right {
  width: 100%;
}
.right2 {
  width: calc(100% - 240px);
}
.main {
  margin: 10px;
  background: #ffffff;
  height: calc(100% - 130px);
}
.el-scrollbar {
  /deep/.el-scrollbar__wrap {
    overflow-x: hidden;
    padding-right: 10px;
    box-sizing: border-box;
  }
}

/deep/.el-tabs__content {
  overflow: visible;
}
</style>
