import Layout from '@/pages/index'
import { decryptDes } from "@/utils/des";
const _import = path => r => require.ensure([], () => r(require('../pages/' + path + '.vue')));

// export const constantRouterMap = [
// { path: '/', redirect: '/login', hidden: true },
// { path: '/login', component: _import('login'), hidden: true },
// ]

function routersList() {
	let userJson = localStorage.getItem('userInfo')
	let hiddenCompany
	hiddenCompany = userJson && JSON.parse(decryptDes(userJson)).icon


	return [
		{ path: '/', redirect: '/login', hidden: true },
		{ path: '/login', component: _import('login'), hidden: true },
		// {path:'/', component:_import('index'), name:'主页',
		// children:[
		{
			path: '/clue',
			redirect: 'clue/index',
			component: Layout,
			name: '线索管理',
			icon: 'iconkujialeqiyezhan_shouqianzixun',
			color: '#ff6600',
			children: [
				{
					path: 'index',
					component: _import('clue/management/clue'),
					name: '售前管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'message',
					component: _import('clue/management/message'),
					name: "线索详情",
					hidden: true
				},
				{
					path: 'sendBack',
					component: _import('clue/sendBack/sendBack'),
					name: "销售退回",
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'returnDetail',
					component: _import('clue/sendBack/returnDetail'),
					name: "销售退回详情",
					hidden: true
				},
				{
					path: 'highSeas',
					component: _import('clue/highseas/highSeas'),
					name: '公海管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'highSeasdetails',
					component: _import('clue/highseas/highSeasdetails'),
					name: '公海管理详情',
					hidden: true
				},
			]
		},
		{
			path: '/sellManagement',
			component: Layout,
			name: '销售管理',
			icon: 'iconxiaoshouguanli',
			color: "#00B0FF",
			children: [
				{
					path: 'customer',
					component: _import('sellManagement/customer/customer'),
					name: '客户管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'customerdetails',
					component: _import('sellManagement/customer/customerdetails'),
					name: '客户全景',
					hidden: true
				},
				{
					path: 'linkman',
					component: _import('sellManagement/linkman/linkman'),
					name: '联系人管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'linkmandetails',
					component: _import('sellManagement/linkman/linkmandetails'),
					name: '联系人管理详情',
					hidden: true
				},
				{
					path: 'opportunity',
					component: _import('sellManagement/chance/opportunity'),
					name: '机会管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'opportunitydetails',
					component: _import('sellManagement/chance/opportunitydetails'),
					name: '机会管理详情',
					hidden: true
				},
				{
					path: 'contract',
					component: _import('sellManagement/contract/contract'),
					name: '合同管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'contractdetails',
					component: _import('sellManagement/contract/contractdetails'),
					name: '合同管理详情',
					hidden: true
				},
				{
					path: 'receivable',
					component: _import('sellManagement/payment/receivable'),
					name: '回款管理',
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'receivablePlandetails',
					component: _import('sellManagement/payment/receivablePlandetails'),
					name: '回款计划详情',
					hidden: true
				},
				{
					path: 'receivableRecorddetails',
					component: _import('sellManagement/payment/receivableRecorddetails'),
					name: '回款记录详情',
					hidden: true
				},
			]
		},
		{
			path: '/afterSale',
			component: Layout,
			name: '售后管理',
			icon: 'iconshouhou',
			color: "#F15685",
			children: [
				{
					path: 'accountant',
					component: _import('afterSale/accounting/accountant'),
					name: '售后客户管理'
				},
				{
					path: 'CustomerPanoramic',
					component: _import('afterSale/accounting/CustomerPanoramic'),
					name: '客户管理详情',
					hidden: true,
					meta: {
						keepAlive: true
					}
				},
				{
					path: 'complaintAdvice',
					component: _import('afterSale/complaintadvice/complaintAdvice'),
					name: '投诉与建议'
				},
				// {
				//     path: 'afterOpportunity',
				//     component: _import('afterSale/afterOpportunity'),
				//     name: '机会管理'
				// },
				{
					path: 'returnVisit',
					component: _import('afterSale/returnVisit'),
					name: '回访管理'
				},
			]
		},
		{
			path: '/admin',
			component: Layout,
			name: '系统权限管理',
			icon: "iconmima",
			color: "#67C23A",
			children: [
				{
					path: 'user',
					component: _import('admin/users/user'),
					name: '角色和权限管理',
				},
				{
					path: 'administrators',
					component: _import('admin/users/administrators'),
					name: '成员管理',
				},
				{
					path: 'position',
					component: _import('admin/users/position'),
					name: '职位管理',
				},
				{
					path: 'department',
					component: _import('admin/users/department'),
					name: '部门管理',
				},
				{
					path: 'remind',
					component: _import('admin/information/remind'),
					name: '提醒详情',
					hidden: true,
				},
				{
					path: 'cipher',
					component: _import('admin/information/cipher'),
					name: '修改密码',
					hidden: true,
				},
				{
					path: 'informations',
					component: _import('admin/information/informations'),
					name: '个人信息',
					hidden: true,
				}]
		},
		{
			path: '/subaccounts',
			component: Layout,
			name: '系统设置',
			icon: 'iconxitong1',
			color: "#FFD700",
			children: [
				// {
				//     path: 'ditch',
				//     component: _import('clue/management/ditch'),
				//     name: '渠道管理',   
				// },
				{
					path: 'ditch',
					component: _import('subaccounts/ditch'),
					name: '渠道管理',
				},
				{
					path: 'businessType',
					component: _import('subaccounts/businessType'),
					name: "业务类型管理"
				},
				{
					path: 'combo',
					component: _import('subaccounts/combo'),
					name: "套餐管理"
				},
				{
					path: 'company',
					component: _import("subaccounts/company"),
					name: "企业管理",
					hidden: !hiddenCompany
				}
			]
		},
		// ]},
	]

}

// export const menuRouter = [...frameIn];
// export default [...constantRouterMap, ...frameIn];
export default routersList;