import Vue from 'vue'
import Vuex from 'vuex'
// import getters from './getters'
import user from './modules/user'

let baseURL = require("../../public/projectConfig")

// let userInfo = this.$store.state.userInfo
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    baseURL,
    userInfo: null
  },
  modules: {
    user
  },
  mutations: {
    userInfo(state, data) {
      state.userInfo = data
    },

  }
})
