import fetch from '@/utils/fetch';

export default {
	// 获取用户信息
	getUserInfo(data) {
		return fetch({
			url: '/getUserInfo',
			method: "get",
			params: data
		})
	},
	// 退出
	logout(data) {
		return fetch({
			url: '/logout',
			method: "get",
			params: data
		})
	},

	// 登陆
	login(data) {
		return fetch({
			url: 'login',
			method: 'get',
			params: data
		})
	},

	// 修改密码
	updatePassword(data) {
		return fetch({
			url: '/updatePassword',
			method: 'get',
			params: data
		})
	},

}