Vue.use(ElementUI)
// Vue.prototype.$message = new DonMessage()

Vue.config.productionTip = false

/**
 * @list userinfo的permissionList是被禁用的接口列表（没有权限）
 * @getPermissionList 用来给list获取值，退出登录main.js不会重新执行
 * 所以需要在login.vue登录成功后重新抓取userinfo赋值给list
 * 
 */
let list = null
function getPermissionList() {
  // let userInfo =  JSON.parse(localStorage.getItem('userInfo'))

  let userInfo = store.state.userInfo

  if (!userInfo) return
  list = userInfo.permissionList
}
getPermissionList()
Vue.prototype.getPermissionList = getPermissionList

// 判断是否给权限
function permissionJudge(value) {
  if (!list) return false
  for (let item of list) {
    if (item === value) return true;

  }
}


// 自定义指令，判断如果没有这个权限，那么隐藏或禁用按钮
Vue.directive('permi', {
  // 当被绑定的元素插入到 DOM 中时触发bind钩子
  bind: function (el, binding) {
    // console.log(binding);

    if (permissionJudge(binding.value)) {

      el.setAttribute("disabled", "disabled")
      el.className += " is-disabled"
      el.title = "您暂无权限"
      // }
    }
  }
});


Vue.prototype.resetSetItem = function (key, newVal) {
  if (key === 'userInfo') {

    // 创建一个StorageEvent事件
    var newStorageEvent = document.createEvent('StorageEvent');
    const storage = {
      setItem: function (k, val) {
        localStorage.setItem(k, val);

        // 初始化创建的事件
        newStorageEvent.initStorageEvent('setItem', false, false, k, null, val, null, null);

        // 派发对象
        window.dispatchEvent(newStorageEvent)
      }
    }
    return storage.setItem(key, newVal);
  }
}

Array.prototype.remove = function (val) {
  var index = this.indexOf(val);
  if (index > -1) {
    this.splice(index, 1);
  }
};

const vm = new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')


export default vm


