<template>
  <div class="hello">
    <div class="logo">
      <img src="../assets/login/maxlogo.png" alt />
    </div>
    <el-container>
      <el-row class="tac">
        <el-col :span="12" width="240px">
          <el-menu :default-active="showPath" class="el-menu-vertical-demo" router unique-opened>
            <div v-for="(item,i) in list" :key="i" class="item-list">
              <div v-if="item.children">
                <el-submenu :index="i+''">
                  <template slot="title">
                    <i
                      class="iconfont"
                      :class="item.icon"
                      :style="{color:item.color}"
                      style="margin-right:20px"
                    ></i>
                    <span style="font-size:16px; color:#323232;">{{item.name}}</span>
                  </template>
                  <div v-for="(ites,i) in item.children" :key="i">
                    <div v-if="!ites.hidden">
                      <el-menu-item
                        :index="item.path+'/'+ites.path"
                        style="padding-left:70px;color:#909090;"
                      >{{ites.name}}</el-menu-item>
                    </div>
                  </div>
                </el-submenu>
              </div>
              <div v-else>
                <el-menu-item :index="item.path">
                  <i
                    class="iconfont"
                    :class="item.icon"
                    :style="{color:item.color}"
                    style="margin-right:20px"
                  ></i>
                  <span slot="title" style="color:#323232;">{{item.name}}</span>
                </el-menu-item>
              </div>
            </div>
          </el-menu>
        </el-col>
      </el-row>
      <el-container></el-container>
    </el-container>
  </div>
</template>

<script>
import routersList from "@/router/routers";
// index左边
export default {
  name: "umen",
  props: {
    msg: String
  },
  data() {
    return {
      list: [],
      fromRoute: sessionStorage.getItem("fromRoute")
    };
  },
  mounted() {
    // let allRoutes = this.$router.options.routes;
    let allRoutes = routersList();
    let list = [];
    allRoutes.forEach(item => {
      //  console.log(x)
      if (!item.hidden) {
        list.push(item);
      }
    });

    this.list = list;
  },
  methods: {},
  computed: {
    showPath() {
      let path = this.$route.path;
      switch (path) {
        case "/clue/message":
          return "/clue/index";
          break;
        case "/sellManagement/customerdetails":
          return "/sellManagement/customer";
          break;
        case "/clue/returnDetail":
          return "/clue/sendBack";
          break;
        case "/clue/highSeasdetails":
          return "/clue/highSeas";
          break;
        case "/sellManagement/linkmandetails":
          return "/sellManagement/linkman";
          break;
        case "/sellManagement/opportunitydetails":
          return "/sellManagement/opportunity";
          break;
        case "/sellManagement/contractdetails":
          return "/sellManagement/contract";
          break;
        case "/sellManagement/receivableRecorddetails":
          return "/sellManagement/receivable";
          break;
        case "/afterSale/CustomerPanoramic":
          return "/afterSale/accountant";
          break;
        case "/sellManagement/receivablePlandetails":
          return "/sellManagement/receivable";
          break;

        default:
          return path;
          break;
      }
    }
  }
};
</script>
