<template>
  <div class="header">
    <div class="header-title">
      <div class="header-list">
        <div style="line-height:57px;">
          <i
            class="iconfont icondaohangshouqi-"
            style="font-size:20px;cursor:pointer;"
            @click="umen"
          ></i>
        </div>
        <p style="line-height:58px;">
          <span>{{msg}}</span>
          <router-link to></router-link>
        </p>
      </div>
      <div class="top">
        <!-- <p class="top-email">
          <router-link :to="{path:'/admin/remind'}">
            <i class="el-icon-message"></i>
          </router-link>
        </p>-->
        <div class="title">
          <el-dropdown trigger="click" @command="handleCommand">
            <span class="el-dropdown-link" style="cursor:pointer;">
              <img :src="src" alt />
              &nbsp;&nbsp;{{userName}}&nbsp;&nbsp;
              <i
                class="el-icon-caret-bottom el-icon--right"
              ></i>
            </span>
            <el-dropdown-menu slot="dropdown" style="width:200px">
              <el-dropdown-item command="/admin/informations">
                <i class="iconfont icongeren"></i>个人信息
              </el-dropdown-item>
              <el-dropdown-item command="/admin/cipher">
                <i class="iconfont iconmima"></i>密码设置
              </el-dropdown-item>
              <el-dropdown-item @click.native="logOut">
                <i class="iconfont icontuichu"></i>安全退出
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="header-content">
      <el-button type="primary" size="small" @click="go">上一页</el-button>
    </div>
  </div>
</template>

<script>
// 头部
import { getToken, removeToken, delCookie } from "@/utils/auth";
import API from "@/api/information";
export default {
  name: "headers",
  props: {
    msg: {
      type: String,
      default: ""
    },
    userName: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      show: false,
      pre: true,
      src: require("../assets/login/toux.jpg")
    };
  },
  methods: {
    handleCommand(command) {
      this.$router.push({
        path: command
      });
    },
    logOut() {
      API.logout()
        .then(res => {
          delCookie("token");
          localStorage.removeItem("userInfo");
          this.$store.commit("userInfo", {});
          this.$message.success("退出成功");
          this.$router.push({ path: "/login" });
        })
        .catch(err => {
          console.error(err);
        });
      // return removeToken();
      // console.log(getToken())
    },
    umen() {
      this.pre = !this.pre;
      // console.log(this.pre)
      this.$emit("showUmen", this.pre);
    },
    go() {
      this.$router.go(-1);
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.iconfont {
  margin-right: 18px;
  vertical-align: middle;
}
.header {
  background: #ffffff;
}
.header-list {
  display: flex;
}
.header-title {
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 16px;
  padding: 0 20px;
  border: 1px solid #ddd;
  background: rgba(153, 204, 255, 1);
  color: #ffffff;
}
.umen-list .el-icon-message {
  margin-right: 14px;
}
.umen-list {
  color: #333;
}
.header-title > p {
  line-height: 60px;
}
.header-content {
  height: 32px;
  padding: 8px;
}
.top {
  display: flex;
}
.top-email {
  line-height: 68px;
  margin-right: 14px;
}
.top-email > a > i {
  font-size: 24px;
  color: #ffffff;
}
.title img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  vertical-align: middle;
}
.title-umen {
  width: 174px;
  height: 174px;
  background: #ffffff;
  position: absolute;
  top: 52px;
  z-index: 99;
  left: -94px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: -4px 4px 10px #ddd;
}
.umen-list > li {
  margin-top: 24px;
  padding: 0 34px;
}
.title {
  position: relative;
  // width: 80px;
  height: 40px;
  margin: 10px;
}
.title-buttom {
  position: absolute;
  top: -18px;
  left: 20px;
}

.el-dropdown-menu {
  .el-dropdown-menu__item {
    padding-left: 40px;
  }
}
</style>
